.hero {
    display: flex;
    flex-direction: column;
    background-image: url(../../assets/mobile/image-hero.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
    padding: 30px 40px;
    position: relative;
    /* margin-bottom: 20px; */
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    margin-bottom: 250px;
}

@media (max-width:750px) {
    .main-nav {
        display: flex;
        flex-direction: column;
        background-color: black;
        padding: 30px 0;
        text-transform: uppercase;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        height: 85vh;
        z-index: 200;
        visibility: hidden;
    }

    .active {
        visibility: visible;
    }

    .main-nav ul {
        margin-top: 120px;
        padding: 10px 0;
    }

    .main-nav ul li {
        display: block;
        padding: 15px 45px;
        list-style: none;
        margin-bottom: 15px;
    }

}

.main-nav ul li:hover {
    text-decoration: underline;
}


.main-nav ul li:last-child {
    list-style: none;
    margin-right: 0px;
}

.main-nav ul li a {
    text-decoration: none;
    color: white;
    font-family: var(--HeaderFont);
    font-weight: 600;
}

.logo img {
    width: 80%;
    /* border: 1px solid red; */
    position: relative;
    z-index: 500;
}

.hamburger img {
    height: 20px;
    position: relative;
    cursor: pointer;
}

.closeMenu {
    position: absolute;
    top: 42px;
    right: 45px;
    cursor: pointer;
}


.header-ms h1 {
    color: white;
    font-family: var(--HeaderFont);
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: 500;
    border: 3px solid white;
    padding: 25px;
    max-width: 500px;
    margin-left: 0;
    margin-top: -50px;
}

@media (min-width:750px) {

    /* .main-nav{
        visibility: visible;
    } */

    .hamburger,
    .closeMenu {
        display: none;
    }

    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .main-nav {
        display: flex;
    }

    .main-nav ul {
        display: flex;
        flex-direction: row;
    }

    .main-nav ul li {
        list-style: none;
        margin-right: 30px;
        margin-bottom: 0;
        font-size: 1rem;
    }

    .main-nav ul li:last-child {
        margin-right: 0px;
    }

    .main-nav ul li a {
        text-decoration: none;
        color: white;
        font-family: var(--HeaderFont);
        font-weight: 600;
    }

    .logo img {
        width: 100%;
        /* border: 1px solid red; */
        position: relative;
        z-index: 500;
    }

    .hero {
        justify-content: flex-start;
        padding: 40px 55px;
        background-image: url(../../assets/desktop/image-hero.jpg);
    }

    .header-ms {
        max-width: 700px;
        text-align: left;
    }

    .header-ms h1 {
        font-size: 4.5rem;
        font-weight: 500;
        border: 3px solid white;
        padding: 30px;
        max-width: 700px;
        margin-left: 0;
    }
}

@media (min-width:1000px) {

    .hero {
        justify-content: flex-start;
        padding: 40px 150px;
    }


}

@media (min-width:1500px) {

    .header-ms h1 {
        font-size: 5.5rem;
        font-weight: 500;
        border: 3px solid rgb(255, 255, 255);
        padding: 50px 60px;
        min-width: 1100px;
        margin-left: 0;
    }
}